.footer a {
  font-family: monospace;
  color: #69788d;
  position: absolute;
  right: 0;
  left: 0;
  bottom: 1px;
  background-color: #fff;
}

@media (max-width: 992px) {
  .footer a {
    font-family: monospace;
    color: #69788d;
    position: initial;
    right: 0;
    left: 0;
    bottom: 5px;
  }
}
